.node {
  padding-left: 3rem;
  padding-bottom: 1.5rem;
  position: relative;
}
.node h3, .node p {
  margin: 0;
}
.node::before {
  content: "";
  width: 1rem;
  height: 1rem;
  background: #A02C2C;
  border: 2px solid #000;
  border-radius: 50%;
  position: absolute;
  top: .3rem;
  left: -8.5px;
}

